.watch-statistics-page{
  width: calc(100% - 3.5rem);
  position: absolute;
  right: 0;
  padding: 2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.grid-container{
  float: left;
  display: grid;
  grid-template-rows: repeat(2,auto);
  grid-template-columns: repeat(2,auto);
  width: 44vw;
  height: 30vw;
}
.graph-grid{
  position: relative;
  width: 16vw;
  height: 11vw;
  display: flex;
  flex-direction: column;
}
.graph-flexbox{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.graph-container {
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 1vw;
    padding: 1vw;
    padding-left: 2vw;
    padding-right: 2vw;
    margin-left: 0;
    background-color: white;
    border-radius: 0.15vw;
    box-shadow: 2px 5px 8px 2px #00000020;
  }
  
  svg {
    width: 35vw;
    height: 25vw;
    background: white;
    overflow: visible;
    margin-bottom: 2rem;
    display: block;
  }
  .tick line {
    visibility: hidden;
  }
  .bar-graph{
    width: 23vw;
    height: 26vw;
    display: inline-block;
  }
  .line-graph{
    width: 40vw;
    height: 23.5vw;
  }
  .donut-graph{
    width: 21vw;
    height: 23.5vw;
  }